import React, { useState, useEffect, useContext, useRef } from "react";

import * as Realm from "realm-web";

import {
  ApolloClient,
  ApolloProvider,
  gql,
  HttpLink,
  InMemoryCache,
  useLazyQuery,
  useMutation
  // useQuery
} from "@apollo/client";

export const APP_ID = "application-0-rugmi";

// Connect to your MongoDB Realm app
const app = new Realm.App(APP_ID);
// Gets a valid Realm user access token to authenticate requests
async function getValidAccessToken() {
  // Guarantee that there's a logged in user with a valid access token
  if (!app.currentUser) {
    // If no user is logged in, log in an anonymous user. The logged in user will have a valid
    // access token.
    await app.logIn(Realm.Credentials.anonymous());
  } else {
    // An already logged in user's access token might be stale. To guarantee that the token is
    // valid, we refresh the user's custom data which also refreshes their access token.
    await app.currentUser.refreshCustomData();
  }
  return app.currentUser.accessToken;
}
// Configure the ApolloClient to connect to your app's GraphQL endpoint
const client = new ApolloClient({
  link: new HttpLink({
    uri: `https://us-east-1.aws.realm.mongodb.com/api/client/v2.0/app/${APP_ID}/graphql`,
    // We define a custom fetch handler for the Apollo client that lets us authenticate GraphQL requests.
    // The function intercepts every Apollo HTTP request and adds an Authorization header with a valid
    // access token before sending the request.
    // credentials: "same-origin",
    fetch: async (uri, options) => {
      const accessToken = await getValidAccessToken();
      options.headers.Authorization = `Bearer ${accessToken}`;
      return fetch(uri, options);
    }
  }),
  cache: new InMemoryCache()
});

const ADD_TODO_MUTATION = gql`
  mutation AddTodo($title: String!) {
    todo: addSimpleTodo(input: $title) {
      _id
      title
      checked
      createAt
      updateAt
    }
  }
`;

const DEFAULT_POOL_TIME = 30;

const TodoContext = React.createContext({});

function AddTodo() {
  const [addTodo, { error, loading, called, data }] = useMutation(
    ADD_TODO_MUTATION
  );

  const [title, setTitle] = useState("");

  const { refreshTodos } = useContext(TodoContext);

  const inputRef = useRef();

  useEffect(() => {
    if (!loading && called) {
      if (typeof refreshTodos === "function") {
        console.log("main", refreshTodos);
        refreshTodos();
        setTitle("");
        inputRef.current.focus();
      }
    }
  }, [loading, called, refreshTodos]);

  return (
    <div>
      <div>
        <input
          ref={inputRef}
          placeholder="Escribe algo por hacer..."
          value={title}
          onChange={(event) => {
            setTitle(event.target.value);
          }}
          onKeyDown={(event) => {
            if (event.key === "Enter") {
              if (title) {
                addTodo({
                  variables: {
                    title
                  }
                });
              }
            }
          }}
        />
        <button
          onClick={() => {
            if (title) {
              addTodo({
                variables: {
                  title
                }
              });
            }
          }}
        >
          Agregar
        </button>
      </div>
      {loading && <span>Agregando...</span>}
      {error && <span style={{ color: "red" }}>{JSON.stringify(error)}</span>}
      {data && (
        <div>
          <span>Se ha agregado "{data.todo.title}"</span>
        </div>
      )}
    </div>
  );
}

const UPDATE_TODO_MUTATION = gql`
  mutation UpdateTodo($query: TodoQueryInput!, $todo: TodoUpdateInput!) {
    todo: updateOneTodo(query: $query, set: $todo) {
      _id
      title
      checked
      createAt
      updateAt
    }
  }
`;

function Todo({ todo }) {
  const [updateTodo, { error, loading, called, data }] = useMutation(
    UPDATE_TODO_MUTATION
  );

  const inputRef = useRef();

  return (
    <div
      key={todo._id}
      style={{
        display: "flex",
        alignItems: "center"
      }}
    >
      <div
        style={{
          paddingRight: "4px"
        }}
      >
        <input
          ref={inputRef}
          disabled={error || loading}
          type="checkbox"
          checked={todo.checked}
          onChange={(event) => {
            // TODO: Update
            updateTodo({
              variables: {
                query: {
                  _id: todo._id
                },
                todo: {
                  checked: event.target.checked,
                  updateAt: new Date()
                }
              }
            });
          }}
        />
      </div>
      <div
        style={{ cursor: "pointer" }}
        onClick={() => {
          inputRef.current.click();
        }}
      >
        <div>
          <span
            style={{
              textDecoration: todo.checked ? "line-through" : "initial"
            }}
          >
            {todo.title}
          </span>
        </div>
        <div>
          <span style={{ fontSize: "0.8em", color: "gray" }}>
            {new Date(
              todo.updateAt || todo.createAt || new Date()
            ).toLocaleString()}
          </span>
        </div>
      </div>
    </div>
  );
}

const TODOS_QUERY = gql`
  query {
    todos {
      _id
      title
      checked
      createAt
      updateAt
    }
  }
`;

function Todos() {
  const [
    fetchTodos,
    { error, loading, data, called, refetch: refreshTodos }
  ] = useLazyQuery(TODOS_QUERY);

  const [count, setCount] = useState(DEFAULT_POOL_TIME);

  const { subscribeRefreshTodos } = useContext(TodoContext);

  useEffect(() => {
    if (!loading && called) {
      if (subscribeRefreshTodos) {
        if (typeof refreshTodos === "function") {
          console.log("updating refresh function");
          subscribeRefreshTodos(() => {
            return () => {
              refreshTodos();
              setCount(DEFAULT_POOL_TIME);
            };
          });
        }
      }
    }
  }, [refreshTodos, loading, called]);

  useEffect(() => {
    fetchTodos();
  }, []);

  useEffect(() => {
    let id = null;

    if (count > 0 && !loading && called) {
      id = setTimeout(() => {
        setCount(count - 1);
      }, 1000);
    }

    if (count <= 0 && !loading && called) {
      console.log("refresh");
      refreshTodos();
      setCount(DEFAULT_POOL_TIME);
    }

    return () => {
      if (id) {
        clearTimeout(id);
      }
    };
  }, [count, called, loading]);

  if (error)
    return <span style={{ color: "red" }}>{JSON.stringify(error)}</span>;

  if (loading) return <span>Cargando...</span>;

  if (!data) return <span>Esperando...</span>;

  return (
    <div>
      <div
        style={{
          paddingBottom: "8px"
        }}
      >
        <button
          disabled={!called}
          onClick={() => {
            console.log("rtt", refreshTodos);
            refreshTodos();
            setCount(DEFAULT_POOL_TIME);
          }}
        >
          Actualizar
        </button>
        <span>Se actualizar√° en {count}s</span>
      </div>
      {data.todos.map((todo) => {
        return <Todo key={todo._id} todo={todo} />;
      })}
    </div>
  );
}

export default function App() {
  const [refreshTodos, setRefreshTodos] = useState(null);

  // useEffect(() => {
  //   console.log("rt", refreshTodos);
  // }, [refreshTodos]);

  return (
    <ApolloProvider client={client}>
      <TodoContext.Provider
        value={{
          refreshTodos,
          subscribeRefreshTodos: setRefreshTodos
        }}
      >
        <div className="App">
          <AddTodo />
          <Todos />
        </div>
      </TodoContext.Provider>
    </ApolloProvider>
  );
}
